  margin-bottom: 0;
}

ul, ol {
  margin-top: 0;
  margin-bottom: 0;
  padding-left: 0;
  list-style: none;
}

img {
  cursor: pointer;
  display: block;
}

address {
  font-style: normal;
}

._list {
  list-style: none;
}

._link {
  color: currentColor;
  text-decoration: none;
}

button {
  cursor: pointer;
  flex-direction: row;
  display: flex;
}

body {
